body {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: Roboto, sans-serif;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    background-color: #041715;
}

/**************************************************/
::-webkit-scrollbar-button {
    width: 0;
    height: 0;
}

::-webkit-scrollbar-track {
    background-color: #eceded;
    border: 1px solid #BA975F;
    border-radius: 5px;
}

::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #BBBBBB;
    border: 1px solid #BA975F;
}

::-webkit-scrollbar-thumb:hover {
    background-color: #BA975F;
}

::-webkit-resizer {
    width: 10px;
    height: 0;
}

::-webkit-scrollbar {
    width: 10px;
}
/**************************************************/

.no-select {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.tea {
    position: relative;
    display: flex;
    width: 100%;
    color: #BA975F;
    flex-direction: column;
    overflow-x: hidden;
    min-width: 360px;
    background-color: #041715;
}

.glyph-coffee,
.glyph-coffee:after {
    height: 23px;
    margin: 0 16px 0 6px;
    content: url(data:image/png;base64,);
}

.glyph-tea,
.glyph-tea:after {
    height: 23px;
    margin: 0 17px 0 8px;
    content: url(data:image/png;base64,);
}

.glyph-sweets,
.glyph-sweets:after {
    height: 23px;
    margin: 0 18px 0 7px;
    content: url(data:image/png;base64,);
}

.glyph-dishes,
.glyph-dishes:after {
    width: 30px;
    height: 23px;
    margin: 0 12px 0 3px;
    content: url(data:image/png;base64,);
}

.glyph-packaging,
.glyph-packaging:after {
    height: 23px;
    margin: 0 18px 0 8px;
    content: url(data:image/png;base64,);
}

.glyph-gift-baskets,
.glyph-gift-baskets:after {
    height: 23px;
    margin: 0 17px 0 7px;
    content: url(data:image/png;base64,);
}

.address {
    position: absolute;
    left: -15px;
    color: #041715;
    font-size: 11px;
    line-height: 12px;
}